import React, { useEffect, useRef, useState } from 'react'
import styled, { useTheme } from 'styled-components'
import { marginBottom } from '../../Gaps'
import WaveSurfer from 'wavesurfer.js';
import mockAudio from '../../../audio/mon amour remix.mp3'
import { PiPlayCircle } from "react-icons/pi";
import { PiPauseCircle } from "react-icons/pi";
import { responsiveText } from '../../PageBlocks';
import { parseBlob } from 'music-metadata-browser';
import { Spinner } from '../../Spinner';
import { isMobileOnly } from 'react-device-detect';
import { AccordionContentH1, AccordionContentH2 } from './CourseContent';




const CourseAudioContainer = styled.div`
    width: 100%;
    display: flex;
    align-items: center;
    ${marginBottom(45)}
`

const WaveBlock = styled.div`
    flex-grow: 1;
`

const PlayButton = styled.div`
    width: 12%;
    aspect-ratio: 1/1;
    color: ${props => props.theme.colors.text};
    display: flex;
    
    align-items: center;
    ${responsiveText(72, 72, 36)}
`

async function getFileMetadata(url: string) {
    const response = await fetch(url, { method: 'HEAD' });
    const contentType = response.headers.get('Content-Type');
    const contentLength = response.headers.get('Content-Length');
    
    return {
      contentType: contentType,
      contentLength: contentLength
    };
  }

function CourseAudio() {

    const waveBlockRef = useRef(null);
    const [waveSurfer, setWaveSurfer] = useState<WaveSurfer | null>(null)
    const [isPlaying, setIsPlaying] = useState(false)
    const [metaData, setMetadata] = useState({ title: 'неизвестен', artist: 'неизвестен' })
    const theme = useTheme()
    const [loading, setLoading]=useState(true)
    const loadAudio = async () => {
        try {
            const response = await fetch(mockAudio);
            const blob = await response.blob();

            if (waveBlockRef.current && !waveSurfer) {
                const wavesurfer = WaveSurfer.create({
                    container: waveBlockRef.current,
                    waveColor: theme.colors.text,
                    progressColor: theme.colors.primary,
                    barGap: 7,
                    barWidth: 5,
                    barRadius: 5,
                    height: isMobileOnly? 45 : 55,
                    cursorWidth: 0
                })

                await wavesurfer.loadBlob(blob)
                setWaveSurfer(wavesurfer)
            }

        } catch (err) {
            console.log(err)
        } finally {
            setLoading(false)
        }
    }

    useEffect(()=>{
        loadAudio();
    }, [])
    const playClickHandler = () => {

        waveSurfer?.playPause()
        setIsPlaying(prev => !prev)
    }

    return (<>
        <AccordionContentH1>Демо</AccordionContentH1>
        <AccordionContentH2>Модуль 1/урок 3</AccordionContentH2>
        <CourseAudioContainer >
            {loading? <Spinner/> : 
            <PlayButton onClick={playClickHandler}>
                {
                    isPlaying ? <PiPauseCircle /> : <PiPlayCircle />
                }
            </PlayButton>}
            <WaveBlock ref={waveBlockRef}></WaveBlock>
        </CourseAudioContainer>
        </>
    )
}

export default CourseAudio